@charset "UTF-8";
@import url("https://fonts.googleapis.com/css?family=Roboto:100,400&display=swap");
.App {
  display: flex;
  justify-content: space-between;
  height: 100vh;
  font-family: "Roboto", sans-serif;
  font-weight: 100;
  letter-spacing: 1px;
  background: radial-gradient(circle at top left, #b3ff00, #047baa);
  overflow: hidden; }
  .App nav {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    text-align: center;
    margin: 20px; }
    .App nav a {
      width: 100%;
      padding: 5px;
      font-size: 18px;
      text-decoration: none; }
      .App nav a:hover {
        font-weight: 500; }
  .App header {
    margin: 0 10% 0 20%;
    padding: 40px;
    background: white;
    width: 370px;
    text-align: center;
    box-shadow: 0px 0px 20px #00000059;
    transition: all 0.25s; }
    .App header.show {
      margin: 0 2% 0 4%; }
  .App .project-entry {
    border-left: 2px solid white;
    padding-left: 30px;
    margin-left: -20px;
    margin-bottom: 20px;
    transition: all 0.25s cubic-bezier(0.61, 0, 0.31, 0.99);
    width: 100%;
    z-index: 2;
    position: relative; }
    .App .project-entry:hover {
      border-left: 5px solid white; }
      .App .project-entry:hover a::after {
        right: -40px;
        opacity: 1; }
  .App .page-content-wrap {
    display: flex;
    color: White;
    width: 0%;
    transition: width 0.5s; }
    .App .page-content-wrap.show {
      width: 100%; }
      .App .page-content-wrap.show .page-content {
        padding: 15px 45px;
        opacity: 1; }
    .App .page-content-wrap .page-content {
      background: #363636;
      height: auto;
      width: auto;
      margin-left: 25px;
      padding: 15px 0px;
      box-shadow: -5px -5px 20px #00000059;
      border: 0px solid black;
      overflow: auto;
      overflow-x: hidden;
      border-top: 20px solid #0000004f;
      flex-grow: 1; }
    .App .page-content-wrap nav {
      text-align: left; }
      .App .page-content-wrap nav a {
        position: relative;
        color: White;
        margin-left: -5px;
        font-size: 20px;
        margin-bottom: -15px;
        letter-spacing: 1px;
        font-weight: 500;
        line-height: 32px;
        border-radius: 10px 0px 10px 0px; }
        .App .page-content-wrap nav a::after {
          content: "→";
          font-size: 30px;
          position: absolute;
          top: 0px;
          right: 0px;
          transition: all 0.25s;
          opacity: 0; }
